.events {
  margin-bottom: 64px;
}

.events__box {
  width: 1024px;
  margin: 0 auto;
}

.events h2 {
  margin: 0;
  margin-bottom: 24px;

  font-weight: bold;
  font-size: 48px;
  line-height: 44px;
  letter-spacing: -0.01em;
  color: #ffffff;
}

.events h2 span:last-child {
  display: none;
}

.events__list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 14px;
}

.events__button {
  margin-right: 8px;
  margin-bottom: 8px;
}

.events__items {
  display: flex;
  flex-wrap: wrap;
}

.events__item {
  margin-bottom: 24px;
}

.events__item:nth-child(3n+2) {
  margin-left: 24px;
  margin-right: 25px;
}

@media (max-width: $tablet-width) {

  .events__box {
    width: 100%;
  }

  .events h2 {
    margin-left: 24px;
  }

  .events h2 span:last-child {
    display: inline;
  }
  .events h2 span:first-child {
    display: none;
  }

  .events__list {
    margin-left: 24px;
  }

  .events__items {
    margin-left: 24px;
  }

  .events__item {
    width: 325px;
    margin-left: 24px;
  }

  .events__item .slick-slide {
    width: 325px;
    outline: none;
  }
}

@media (max-width: $mobile-width) {

  .events {
    margin-bottom: 28px;
  }

  .events h2 {
    margin-left: 16px;
    margin-bottom: 16px;
  }

  .events__button {
    margin-right: 4px;
    margin-bottom: 4px;
  }

  .events__list {
    margin-left: 16px;
    margin-bottom: 9px;
  }

  .events__items {
    margin-left: 16px;
  }

  .events__item {
    width: 220px;
    margin-left: 16px;
  }

  .events__item:nth-child(3n+2) {
    margin-left: 16px;
    margin-right: 0;
  }

  .events__item .slick-slide {
    width: 220px;
  }
}
